@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

:root {
  --bg-top: #0b0b0c;
  --bg-bottom: #0f1012;
  --panel: #16181d;
  --border: #2a2f37;
  --text-primary: #EAECEF;
  --text-secondary: #B8C0CC;
  --accent: #D7B38C;
  --glow: rgba(215, 179, 140, 0.35);
  --success: #10B981;
  --warning: #F59E0B;
  --danger: #EF4444;
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.5);
  --shadow-glow: 0 0 20px var(--glow);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none !important;
}

*:focus {
  outline: none !important;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: var(--bg-bottom);
  color: var(--text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.6;
}

/* Enhanced Flashcards Page with Dynamic Theming */
.flashcards-page {
  min-height: 100vh;
  background: var(--bg-bottom);
  font-family: 'Inter', sans-serif;
  color: var(--text-primary);
  position: relative;
  overflow-x: hidden;
}

.flashcards-page::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(ellipse at top left, var(--glow) 0%, transparent 60%),
    radial-gradient(ellipse at bottom right, color-mix(in srgb, var(--accent) 8%, transparent) 0%, transparent 50%),
    linear-gradient(135deg, color-mix(in srgb, var(--bg-top) 95%, transparent) 0%, color-mix(in srgb, var(--bg-bottom) 98%, transparent) 100%),
    url("https://www.transparenttextures.com/patterns/shattered-dark.png");
  pointer-events: none;
  z-index: -1;
}

/* Enhanced Header */
.flashcards-header {
  background: var(--panel);
  backdrop-filter: blur(20px) saturate(1.2);
  border-bottom: 2px solid var(--border);
  padding: 25px 0;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: var(--shadow-lg);
}

.flashcards-header::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--accent);
  opacity: 0.3;
}

.header-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* Enhanced Header Buttons */
.back-btn, .chat-btn {
  background: var(--panel);
  border: 1px solid var(--border);
  color: var(--accent);
  padding: 10px 18px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  font-size: 11px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

.back-btn::before, .chat-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.back-btn:hover, .chat-btn:hover {
  background: color-mix(in srgb, var(--panel) 80%, var(--accent) 20%);
  border-color: var(--accent);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md), var(--shadow-glow);
}

.back-btn:hover::before, .chat-btn:hover::before {
  left: 100%;
}

/* Enhanced Flashcards Title */
.flashcards-title {
  font-size: 28px;
  font-weight: 800;
  letter-spacing: -1px;
  color: var(--accent);
  text-transform: lowercase;
  position: relative;
}

.flashcards-header .flashcards-title.clickable-logo {
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 10px 15px;
  user-select: none;
  position: relative;
}

.flashcards-header .flashcards-title.clickable-logo::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: color-mix(in srgb, var(--accent) 10%, transparent);
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.flashcards-header .flashcards-title.clickable-logo:hover::before {
  opacity: 1;
}

.flashcards-header .flashcards-title.clickable-logo:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md), var(--shadow-glow);
}

.header-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.profile-picture {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid var(--accent);
  object-fit: cover;
}

.user-name {
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--accent);
  text-transform: capitalize;
}

.google-badge {
  background-color: #4285f4;
  color: white;
  font-size: 8px;
  padding: 1px 4px;
  border-radius: 0;
  letter-spacing: 0.5px;
  font-weight: 600;
}

.logout-btn {
  background: var(--panel);
  border: 1px solid var(--border);
  color: var(--accent);
  padding: 10px 18px;
  font-size: 11px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

.logout-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.logout-btn:hover {
  background: color-mix(in srgb, var(--panel) 80%, var(--accent) 20%);
  border-color: var(--accent);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md), var(--shadow-glow);
}

.logout-btn:hover::before {
  left: 100%;
}

.flashcards-container {
  display: flex;
  min-height: calc(100vh - 80px);
  background: var(--bg-bottom);
}

/* Sidebar Navigation */
.flashcards-sidebar {
  width: 280px;
  background: var(--panel);
  border-right: 2px solid var(--border);
  padding: 30px 0;
  position: sticky;
  top: 80px;
  height: calc(100vh - 80px);
  overflow-y: auto;
  box-shadow: var(--shadow-md);
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 0;
  padding: 0;
  list-style: none;
}

.sidebar-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 24px;
  border: none;
  background: transparent;
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.5px;
  text-transform: capitalize;
  border-left: 4px solid transparent;
  position: relative;
  overflow: hidden;
}

.sidebar-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: color-mix(in srgb, var(--accent) 10%, transparent);
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: -1;
}

.sidebar-item:hover {
  color: var(--accent);
  background: color-mix(in srgb, var(--panel) 80%, var(--accent) 10%);
  transform: translateX(4px);
}

.sidebar-item.active {
  color: var(--accent);
  background: color-mix(in srgb, var(--panel) 90%, var(--accent) 20%);
  border-left-color: var(--accent);
  font-weight: 700;
  box-shadow: inset -4px 0 0 var(--accent);
}

.sidebar-icon {
  font-size: 20px;
  min-width: 24px;
}

.sidebar-label {
  flex: 1;
  text-align: left;
}

/* Main Content */
.flashcards-main {
  flex: 1;
  padding: 50px 60px;
  overflow-y: auto;
  min-height: calc(100vh - 80px);
  max-height: calc(100vh - 80px);
}

.generator-section {
  text-align: center;
  margin-bottom: 80px;
  position: relative;
}

.cards-section {
  position: relative;
}

.statistics-section {
  position: relative;
}

.section-title {
  font-size: 36px;
  font-weight: 800;
  letter-spacing: -1px;
  margin-bottom: 40px;
  color: var(--accent);
  text-transform: lowercase;
}

/* Cards Section - Renamed History */
.cards-section .history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.history-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

/* Statistics Panel Styles */
.stats-main-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 50px;
}

.stat-card-large {
  background: var(--panel);
  border: 1px solid var(--border);
  border-radius: 0;
  padding: 30px 20px;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--shadow-sm);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  position: relative;
  overflow: hidden;
}

.stat-card-large::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, color-mix(in srgb, var(--accent) 5%, transparent), transparent);
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 0;
}

.stat-card-large:hover {
  border-color: var(--accent);
  box-shadow: var(--shadow-md), var(--shadow-glow);
  transform: translateY(-4px);
}

.stat-card-large:hover::before {
  opacity: 1;
}

.stat-value {
  font-size: 28px;
  font-weight: 800;
  color: var(--accent);
  letter-spacing: -1px;
  position: relative;
  z-index: 1;
}

.stat-label {
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
}

.stats-details {
  background: var(--panel);
  border: 1px solid var(--border);
  border-radius: 0;
  padding: 30px;
  margin-top: 30px;
  box-shadow: var(--shadow-sm);
}

.stats-subtitle {
  font-size: 18px;
  font-weight: 700;
  color: var(--accent);
  margin-bottom: 24px;
  text-transform: lowercase;
}

.stats-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background: color-mix(in srgb, var(--accent) 5%, transparent);
  border-radius: 0;
  border-left: 3px solid var(--accent);
}

.info-label {
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.info-value {
  font-size: 16px;
  font-weight: 700;
  color: var(--accent);
}

/* Auto-save option */
.generation-options {
  margin-bottom: 25px;
  text-align: center;
}

.auto-save-option {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: var(--text-secondary);
  font-size: 14px;
  cursor: pointer;
  transition: color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  padding: 0;
  background: transparent;
  border: none;
  box-shadow: none;
}

.auto-save-option:hover {
  color: var(--accent);
}

.auto-save-option input[type="checkbox"] {
  width: 18px;
  height: 18px;
  accent-color: var(--accent);
  cursor: pointer;
}

/* Enhanced Mode Selector */
.mode-selector {
  margin-bottom: 2.5rem;
}

.mode-tabs {
  display: flex;
  background: var(--panel);
  border: 2px solid var(--border);
  padding: 4px;
  gap: 4px;
  max-width: 450px;
  margin: 0 auto;
  box-shadow: var(--shadow-md);
}

.mode-tab {
  flex: 1;
  padding: 14px 26px;
  border: none;
  background: transparent;
  color: var(--accent);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  position: relative;
  overflow: hidden;
}

.mode-tab::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.mode-tab:hover {
  background: color-mix(in srgb, var(--panel) 80%, var(--accent) 20%);
  transform: translateY(-2px);
}

.mode-tab:hover::before {
  left: 100%;
}

.mode-tab.active {
  background: var(--accent);
  color: var(--bg-top);
  font-weight: 700;
  box-shadow: var(--shadow-md), var(--shadow-glow);
}

/* Enhanced Input Section */
.input-section {
  display: flex;
  gap: 20px;
  max-width: 700px;
  margin: 0 auto;
  position: relative;
}

.topic-input {
  flex: 1;
  background: var(--bg-bottom);
  border: 1px solid var(--border);
  color: var(--text-primary);
  padding: 16px 22px;
  font-size: 15px;
  outline: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  box-shadow: var(--shadow-sm);
}

.topic-input::placeholder {
  color: var(--text-secondary);
  font-style: italic;
}

.topic-input:focus {
  border-color: var(--accent);
  background: var(--panel);
  box-shadow: 0 0 0 3px color-mix(in srgb, var(--accent) 15%, transparent);
}

.generate-btn {
  background: var(--accent);
  border: 2px solid var(--accent);
  color: var(--bg-top);
  padding: 16px 32px;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-md), var(--shadow-glow);
}

.generate-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  transition: left 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.generate-btn:hover:not(:disabled) {
  background: color-mix(in srgb, var(--accent) 85%, white);
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg), 0 0 30px var(--glow);
}

.generate-btn:hover:not(:disabled)::before {
  left: 100%;
}

.generate-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Enhanced Chat History Section */
.chat-history-section {
  background: var(--panel);
  border: 1px solid var(--border);
  border-radius: 0;
  padding: 30px;
  margin-top: 20px;
  box-shadow: var(--shadow-md);
}

.chat-history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  flex-wrap: wrap;
  gap: 16px;
}

.chat-history-header h3 {
  color: var(--accent);
  margin: 0;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.selection-controls {
  display: flex;
  gap: 12px;
}

.select-all-btn,
.clear-all-btn {
  padding: 10px 18px;
  border: 1px solid var(--border);
  background: var(--panel);
  color: var(--accent);
  font-size: 11px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  box-shadow: var(--shadow-sm);
}

.select-all-btn:hover,
.clear-all-btn:hover {
  background: color-mix(in srgb, var(--panel) 80%, var(--accent) 20%);
  border-color: var(--accent);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.select-all-btn:disabled,
.clear-all-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Enhanced Chat Sessions Grid */
.chat-sessions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
  max-height: 350px;
  overflow-y: auto;
  padding: 10px;
}

.chat-sessions-grid::-webkit-scrollbar {
  width: 8px;
}

.chat-sessions-grid::-webkit-scrollbar-track {
  background: var(--panel);
}

.chat-sessions-grid::-webkit-scrollbar-thumb {
  background: var(--accent);
  border-radius: 0;
  border: 2px solid var(--panel);
}

.chat-sessions-grid::-webkit-scrollbar-thumb:hover {
  background: color-mix(in srgb, var(--accent) 80%, white);
}

.chat-session-card {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px;
  background: var(--bg-bottom);
  border: 1px solid var(--border);
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--shadow-sm);
  position: relative;
  overflow: hidden;
}

.chat-session-card::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  background: var(--accent);
  transform: scaleY(0);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.chat-session-card:hover {
  background: var(--panel);
  border-color: var(--accent);
  transform: translateY(-3px);
  box-shadow: var(--shadow-md);
}

.chat-session-card:hover::before {
  transform: scaleY(1);
}

.chat-session-card.selected {
  background: color-mix(in srgb, var(--accent) 15%, var(--bg-bottom));
  border-color: var(--accent);
  box-shadow: var(--shadow-md), inset 0 0 20px var(--glow);
}

.chat-session-card.selected::before {
  transform: scaleY(1);
}

.session-checkbox input {
  width: 20px;
  height: 20px;
  accent-color: var(--accent);
  cursor: pointer;
}

.session-info {
  flex: 1;
}

.session-title {
  color: var(--accent);
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 6px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 1.4;
}

.session-date {
  color: var(--text-secondary);
  font-size: 12px;
  letter-spacing: 0.5px;
  font-weight: 500;
}

/* Chat Generate Section */
.chat-generate-section {
  text-align: center;
  padding-top: 25px;
  border-top: 1px solid var(--border);
}

.selection-summary {
  margin-bottom: 20px;
}

.selection-summary p {
  color: var(--text-secondary);
  font-size: 14px;
  margin: 0;
  letter-spacing: 0.5px;
  font-weight: 500;
}

/* No Chats Message */
.no-chats {
  text-align: center;
  padding: 50px 20px;
  color: var(--text-secondary);
}

.no-chats p {
  font-size: 16px;
  margin: 0;
  line-height: 1.6;
}

.link-btn {
  background: none;
  border: none;
  color: var(--accent);
  text-decoration: underline;
  cursor: pointer;
  font-size: inherit;
  font-family: inherit;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
}

.link-btn:hover {
  color: color-mix(in srgb, var(--accent) 80%, white);
}

/* Enhanced Flashcards Section */
.flashcards-section {
  background: var(--panel);
  backdrop-filter: blur(20px) saturate(1.2);
  border: 1px solid var(--border);
  border-radius: 0;
  padding: 50px;
  box-shadow: var(--shadow-lg);
  position: relative;
}

.flashcards-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent);
  opacity: 0.3;
}

.flashcard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.flashcard-title-section {
