/* UTILITY CLASSES */
.text-center {
  text-align: center;
}

.hidden {
  display: none;
}

.fade-in {
  animation: fadeIn 0.6s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-up {
  animation: slideUp 0.4s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* FOCUS STYLES FOR ACCESSIBILITY */
.tab-btn:focus,
.mode-tab:focus,
.generate-btn:focus,
.control-btn:focus,
.study-btn:focus,
.delete-btn:focus,
.refresh-btn:focus,
.get-started-btn:focus,
.rename-btn:focus,
.rename-btn-small:focus,
.rename-save-btn:focus,
.rename-cancel-btn:focus,
.back-btn:focus,
.chat-btn:focus,
.logout-btn:focus {
