/* FIXED FLASHCARD STYLES - SIMPLIFIED FLIP LOGIC */
.flashcard {
  width: 600px;
  height: 350px;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s ease;
  cursor: pointer;
}

.flashcard.flipped {
  transform: rotateY(180deg);
}

.flashcard-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}

.flashcard-front,
.flashcard-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 40px;
  background: var(--panel);
  border: 1px solid var(--border);
  box-shadow: var(--shadow-lg);
}

.flashcard-front {
  transform: rotateY(0deg);
}

.flashcard-back {
  transform: rotateY(180deg);
  background: color-mix(in srgb, var(--accent) 15%, var(--panel));
  border-color: var(--accent);
}

.card-label {
  position: absolute;
  top: 18px;
  left: 25px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.2px;
  color: var(--text-secondary);
  text-transform: uppercase;
}

.card-content {
  font-size: 18px;
  line-height: 1.6;
  color: var(--text-primary);
  text-align: center;
  width: 100%;
  word-wrap: break-word;
  font-weight: 500;
}

.flip-hint {
  position: absolute;
  bottom: 18px;
  font-size: 11px;
  color: var(--text-secondary);
